<template>
    <div>
        <action-editor v-if="show && cities.size > 0 && user.permission.indexOf(6) !== -1"></action-editor>
        <div class="content" v-if="user.permission.length > 0 && user.permission.indexOf(6) === -1">
            <div class="main-title">
                <div class="main-title__wrap main-title--nowrap">
                    <div class="h1">Конструктор акции</div>
                </div>
            </div>
            <section class="container">
                <div class="container__full">
                    <div class="wrapper-content">
                        <div class="sect-blank">
                            <div class="wrap text-center">
                                <div class="text-size-l text-bolder text-color-red mt16">
                                    У вас нет прав <br> доступа в этот раздел
                                </div>
                            </div>
                        </div>
                        <!-- end sect-blank -->
                    </div>
                </div>
            </section>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import ActionEditor from '@/components/partner/actions/ActionEditor.vue';

    export default {
        metaInfo() {
            return {
                title: 'Конструктор акции',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "ActionEdit",
        components: {
            ActionEditor
        },
        data() {
            return {
                show: false,
            }
        },
        computed: {
            ...mapState({
                cities: state => state.cities.items,
                user: state => state.users.user,
            }),
        },
        created(){
            let self = this;
            if(this.$route.params.id === 'new'){
                this.$store.dispatch('partnerActions/initCreate').then(() => {
                    self.show = true;
                });
            } else {
                if(this.cities.size > 0){
                    this.$store.dispatch('partnerActions/initEdit', {route: this.$route}).then( () => {
                        this.$store.dispatch('partnerActions/setForm', {route: this.$route}).then(() => {
                            self.show = true;
                        })
                    });
                } else {
                    let unsubscribe = this.$store.subscribe((mutation, state) => {
                        if(mutation.type === 'cities/setItems') {
                            this.$store.dispatch('partnerActions/initEdit', {route: this.$route}).then( () => {
                                this.$store.dispatch('partnerActions/setForm', {route: this.$route}).then(() => {
                                    self.show = true;
                                })
                            });
                            unsubscribe();
                        }
                    });
                }

            }

        }
    }
</script>

