<template>
    <div class="content">
        <div class="main-title">
            <div class="main-title__wrap main-title--nowrap">
                <div class="h1">{{ title }}</div>
            </div>
            <div class="main-title__right">
                <span class="text-color-grey" v-if="user.permission.indexOf(6) === -1"><span class="i i-lock3 i--dark-trans mr4"></span>У вас нет доступа к созданию и редактированию акций</span>
            </div>
        </div>


        <section class="container">
            <div class="full">
                <div class="coupon-list mt0">
                    <div class="coupon-stat" v-for="action in actions" :key="action.id" v-if="action.id">
                        <div class="coupon-stat__wrap">
                            <div class="coupon">
                                <tooltip
                                     :direction="'left-center-direction'"
                                     :add-class-wrap="'coupon__bookmark-wrap'"
                                     :add-style="{width: 248 + 'px', 'top': '67px', 'right': '126px'}">
                                    <div slot="tooltipBtn" class="coupon__bookmark">
                                        <div class="wrap">
                                            <span class="d">−{{ action.discountFrom }}%</span>
                                            <span class="p">
                                                ≈{{ action.realSavingsFormat }}
                                                <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                                            </span>
                                        </div>
                                    </div>

                                    <div slot="tooltipContent" style="font-size: 16px; ">
                                        При обращении к этому<br>
                                        поставщику средний чек<br>
                                        составляет <strong>{{ action.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>,<br>
                                        а экономия – около
                                        <strong>{{ action.realSavingsFormat }}
                                        <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>
                                    </div>
                                </tooltip>

                                <router-link v-if="action.isEmptyDetail" :to="'/partner/coupon-edit/id'+action.id" class="coupon__link"></router-link>
                                <router-link v-else :to="'/partner/coupons/'+currentStatus+'/id'+action.id" class="coupon__link"></router-link>

                                <div class="coupon__wrap">
                                    <div class="coupon__img" v-if="action.image" :style="{ backgroundImage: 'url(//api.zemskidki.ru/'+action.image+')' }"></div>

                                    <div class="coupon__img --empty" v-else>
                                        <span class="coupon__img__empty">
                                            <span class="icon">
                                                <span class="i i-photo"></span>
                                            </span>
                                            <span class="t">нет фото</span>
                                        </span>
                                    </div>


                                    <div class="coupon__content">

                                        <div class="coupon__stat">
                                            <span class="elem --views">
                                                <span class="i i-eye"></span>
                                                <span class="t">{{ action.showCounterFormat }}</span>
                                            </span>

                                            <span class="elem --buy" v-if="action.statistics">
                                                <span class="i i-cart"></span>
                                                <span class="t">{{ action.statistics.sold }}</span>
                                            </span>

                                            <!-- <span class="elem --comment" v-if=" item.action.countOpinions > 0">
                                                <span class="i i-comment"></span>
                                                <span class="t">{{  action.countOpinions }}</span>
                                            </span> -->
                                        </div>

                                        <div class="coupon__name" v-html="action.name"></div>


                                        <div class="coupon__company" v-html="partner.name"></div>



                                        <div class="w">
                                            <div class="coupon__economy">
                                                <div class="t">
                                                    Экономия

                                                    <tooltip :direction="'bottom-right-direction'"
                                                             :add-class-wrap="'dib mt-2'"
                                                             :add-style="{width: 376 + 'px'}">
                                                        <span slot="tooltipBtn" class="i i-question3 icon-btn"></span>

                                                        <div slot="tooltipContent">
                                                            <p><strong>Как рассчитывается экономия? Очень просто:</strong></p>

                                                            <ul class="ul-disc-small">
                                                                <li>Средний чек у данного поставщика составляет {{ action.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                                                <li>Предоставляемая скидка составляет {{ action.discountFrom }}%</li>
                                                                <li>{{ action.discountFrom }}% от {{ action.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span> – это примерно {{ action.realSavingsFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                                            </ul>

                                                            <p>То есть, при обращении к данному поставщику экономия обычно составляет ≈{{ action.realSavingsFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></p>
                                                        </div>
                                                    </tooltip>
                                                </div>

                                                <div class="p" v-if="action.realSavingsFormat">
                                                    ≈{{ action.realSavingsFormat }}
                                                    <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                                                </div>
                                                <div class="p" v-else>
                                                    ?
                                                </div>
                                            </div>

                                            <div class="coupon__info" v-if="action.averageCheckFormat">
                                                Средний чек
                                                {{ action.averageCheckFormat }}
                                                <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>,

                                                скидка
                                                <span class="n --text" v-if="action.instrumentAction">Бесплатная <br> доставка</span>
                                                <span class="n" v-else>{{ action.discountFrom }}%</span>
                                            </div>
                                            <div class="coupon__info" v-else>
                                                Средний чек
                                                ?,
                                                скидка
                                                <span class="n">?</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <!-- end coupon -->
                        </div>
                        <!-- end coupon-stat__wrap -->


                        <div class="coupon-stat__control">
                            <div class="link" v-if="+action.status === statuses.develop && user.permission.indexOf(6) !== -1">
                                <submit-link-icon class="link__icon"
                                                  icon="send"
                                                  @click.native="setModerate(action)"
                                                  :type="action.buttons.moderate.state">
                                    <span class="t">Опубликовать</span>
                                </submit-link-icon>
                            </div>

                            <!--
                            <div class="link">
                                <submit-link-icon class="link__icon"
                                                  icon="send"
                                                  @click.native="setModerate(action)"
                                                  :type="action.buttons.moderate.state">
                                    <span class="t">Опубликовать</span>
                                </submit-link-icon>
                            </div> -->

                            <!-- <div class="link">
                                <a href="">
                                    <span class="w">
                                        <span class="i i-close2"></span>
                                    </span>
                                    <span class="t">Отменить публикацию</span>
                                </a>
                            </div> -->
                            <div class="link" v-if="+action.status === statuses.active && action.lk_status_publicate == 'NEW' && user.permission.indexOf(6) !== -1">
                                <a href="javascript:void(0);" @click="openPartnerCouponsCancel(action)" class="link__icon">
                                    <span class="w">
                                        <span class="i i-close2"></span>
                                    </span>
                                    <span class="t">Отменить публикацию</span>
                                </a>
                            </div>
                            <div class="link" v-if="+action.status === statuses.active && action.lk_status_publicate != 'NEW' && user.permission.indexOf(6) !== -1">
                                <a href="javascript:void(0);" @click="openPartnerCouponsStop(action)" class="link__icon">
                                    <span class="w">
                                        <span class="i i-pause3"></span>
                                    </span>
                                    <span class="t">Приостановить</span>
                                </a>
                            </div>

                            <div class="link" v-if="+action.status === statuses.archive && user.permission.indexOf(6) !== -1">
                                <submit-link-icon class="link__icon"
                                                  icon="send"
                                                  @click.native="activeAction(action)"
                                                  :type="action.buttons.active.state">
                                    <span class="t">Опубликовать повторно</span>
                                </submit-link-icon>
                            </div>

                            <div class="link" v-if="(+action.status === statuses.develop || +action.status === statuses.active || +action.status === statuses.archive)  && user.permission.indexOf(6) !== -1">
                                <router-link :to="'/partner/coupon-edit/id'+action.id" class="link__icon">
                                    <span class="w">
                                        <span class="i i-pencil"></span>
                                    </span>
                                    <span class="t">Редактировать</span>
                                </router-link>
                                <!--<a v-else-if="+action.status === statuses.archive" href="javascript:void(0);" @click="openPartnerCouponsEditStopped(action)" class="link__icon">
                                    <span class="w">
                                        <span class="i i-pencil"></span>
                                    </span>
                                    <span class="t">Редактировать</span>
                                </a>
                                <a v-else-if="+action.status === statuses.active" href="javascript:void(0);" @click="openPartnerCouponsEdit(action)" class="link__icon">
                                    <span class="w">
                                        <span class="i i-pencil"></span>
                                    </span>
                                    <span class="t">Редактировать</span>
                                </a>-->
                            </div>
                            <div class="link" v-if="user.permission.indexOf(6) !== -1">
                                <submit-link-icon class="link__icon"
                                                  icon="copy"
                                                  @click.native="copyAction(action)"
                                                  :type="action.buttons.copy.state">
                                    <span class="t">Создать копию</span>
                                </submit-link-icon>
                            </div>


                            <div class="link"  v-if="+action.status === statuses.active || +action.status === statuses.archive">
                                <a href="javascript:void(0);" @click="openPartnerCouponsStat(action)" class="link__icon">
                                    <span class="w">
                                        <span class="i i-stat"></span>
                                    </span>
                                    <span class="t">Показать статистику</span>
                                </a>
                            </div>

                            <div class="link" v-if="(+action.status === statuses.develop || +action.status === statuses.archive) && user.permission.indexOf(6) !== -1">
                                <a href="javascript:void(0);" @click="openPartnerCouponsRemove(action)" class="link__icon">
                                    <span class="w">
                                        <span class="i i-trash"></span>
                                    </span>
                                    <span class="t">Удалить</span>
                                </a>
                            </div>

                            <!--<div class="text" v-if="+action.status !== statuses.develop && user.permission.indexOf(6) !== -1">
                                <div class="text__icon">
                                    <span class="icon">
                                        <span class="i i-info"></span>
                                    </span>
                                    <span class="t" v-if="+action.status === statuses.active">
                                        <span class="t__title" >Опубликована {{ action.statusDate}}</span>
                                    </span>
                                    <span class="t" v-if="+action.status === statuses.archive">
                                        <span class="t__title" >Приостановлена {{ action.statusDate}}</span>
                                    </span>
                                    <span class="t" v-if="+action.status === statuses.ban">
                                        <span class="t__title">Удалена {{ action.statusDate}}</span>

                                        <span class="t__note">С удалённой акцией нельзя ничего делать.
Но вы можете создать копию и делать с&nbsp;ней что хотите...</span>

                                        <span class="t__note --red" v-if="action.banReason">Эта акция заблокирована модератором {{ action.statusDate }} Подробности на mailbox@gmail.com</span>

                                    </span>
                                </div>
                            </div>-->

                            <div class="text" v-if="+action.status === statuses.ban && user.permission.indexOf(6) !== -1">
                                <div class="text__icon">
                                    <span class="icon">
                                        <span class="i i-warning i--orange"></span>
                                    </span>

                                    <span class="t">
                                        <span class="t__title text-size-m text-color-orange">Заблокирована модератором</span>
                                        <span class="mt8 t__note">Дата блокировки: {{action.statusDate}} <br> Подробности на {{action.providerEmail}}</span>
                                    </span>
                                </div>
                            </div>
                            <div class="text" v-if="+action.status === statuses.active">
                                <div class="text__icon" v-if="action.lk_status_publicate == 'NEW'">
                                    <span class="icon">
                                        <span class="i i-gear i--yellow"></span>
                                    </span>

                                    <span class="t">
                                        <span class="t__title text-size-m text-color-yellow">На проверке</span>
                                        <span class="mt8 t__note">Акция находится на срочной проверке и будет опубликована в ближайшее время</span>
                                    </span>
                                </div>
                                <div class="text__icon" v-if="action.lk_status_publicate == 'CHECK'">
                                    <span class="icon">
                                        <span class="i i-gear i--yellow"></span>
                                    </span>

                                    <span class="t">
                                        <span class="t__title text-size-m text-color-yellow">На проверке</span>
                                        <span class="mt8 t__note">Текущая редакция находится на проверке.<br> Покупатели пока видят старую версию, опубликованную {{action.publicate_date}}</span>
                                    </span>
                                </div>
                                <div class="text__icon" v-if="action.lk_status_publicate == 'REJECTED'">
                                    <span class="icon">
                                        <span class="i i-close2 i--orange"></span>
                                    </span>

                                    <span class="t">
                                        <span class="t__title text-size-m text-color-orange">Редакция отклонена</span>
                                        <span class="mt8 t__note">Текущая редакция отклонена модератором,<br> подробности на {{partner.email}}. На данный момент покупатели видят старую версию от {{action.publicate_date}}</span>
                                    </span>
                                </div>
                            </div>
                        </div>
                        <!-- end coupon-stat__control -->
                    </div>
                    <!-- end coupon-stat -->

                </div>
                <!-- end coupon-list -->


                <div class="wrapper-content" v-if="!isLoad && actions.length === 0">
                    <div class="sect-blank">
                        <div class="wrap text-center">
                            <div class="text-size-l text-bolder text-color-grey mt16">Здесь пока ничего нет...</div>
                        </div>

                        <div class="mini-zems"></div>
                    </div>
                    <!-- end sect-blank -->
                </div>
                <div v-if="isLoad" class="coupon-stat">
                    <div class="coupon-stat__wrap">
                        <catalog-ghost></catalog-ghost>
                    </div>
                    <div class="coupon-stat__control"></div>
                </div>
            </div>
            <!-- end container__full -->

        </section>
        <!-- end container -->

        <action-modals :action="currentAction"></action-modals>

    </div>
</template>

<script>
    import ListItem from '@/components/partner/actions/ListItem.vue'
    import {flyToFavorite} from '@/helpers/animate';
    import Tooltip from '@/components/Tooltip.vue';
    import Popup from '@/components/modal/Popup.vue';
    import {mapState} from 'vuex';
    import PartnerCouponsStat from '@/components/modal/PartnerCouponsStat.vue';
    import ActionModals from '@/components/partner/actions/ActionModals.vue';
    import SubmitLinkIcon from '@/components/forms/SubmitLinkIcon.vue'
    import Loader from '@/components/Loader.vue';
    import CatalogGhost from '@/components/catalog/CatalogGhost.vue'

    export default {
        metaInfo() {
            return {
                title: this.title,
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Actions",
        components: {
            ListItem,
            Tooltip,
            Popup,
            ActionModals,
            PartnerCouponsStat,
            SubmitLinkIcon,
            Loader,
            CatalogGhost,
        },
        created() {
            this.$store.commit('partnerActions/setItems', {items:[]});
            this.$store.dispatch('partnerActions/getItems',{status:this.currentStatus});
        },
        mounted() {
        },
        data() {
            return {
                currentAction: false,
                buttons: {},
            }
        },
        watch: {
            '$route': 'fetchData',
            isLoad(after,before){
              if(after == false){
                let self = this;
                setTimeout(function(){
                  self.checkOffset();
                },100);
              }
            }
        },
        computed: {
            absolute: {
              get(){
                return this.$store.getters[ 'partnerActions/getTechsupportAbsolute' ]
              },
              set( value ){
                this.$store.commit('partnerActions/setTechsupportAbsolute', value);
              },
            },
            ...mapState({
                actions: state => state.partnerActions.items,
                defaultStatus: state => state.partnerActions.defaultStatusCode,
                statuses: state => state.partnerActions.statuses,
                isLoad: state => state.partnerActions.isLoad,
                user: state => state.users.user,
                partner: state => state.partner.current,
                popups: state => state.popups.items,
                cities: state => state.cities.items,
            }),
            currentStatus() {
                return this.$route.params.status ? this.$route.params.status : this.defaultStatus
            },
            title(){
                let title = '';

                switch (this.statuses[this.$route.params.status]) {
                    case this.statuses.develop:
                        title = 'Акции в разработке';
                        break;
                    case this.statuses.active:
                        title = 'Опубликованные акции';
                        break;
                    case this.statuses.archive:
                        title = 'Приостановленные акции';
                        break;
                    case this.statuses.deleted:
                        title = 'Удалённые акции';
                        break;
                    case this.statuses.ban:
                        title = 'Заблокированные акции';
                        break;
                    default:
                        title = 'Акции в разработке';
                        break;
                }
                return title;
            }
        },
        methods: {
            getRectTop(el){
              let rect = el.getBoundingClientRect();
              return rect.top;
            },
            checkOffset() {
              let socialFloat = document.querySelector('#techsupport');
              let footer = document.querySelector('.footer');
              if((this.getRectTop(socialFloat) + document.body.scrollTop) + socialFloat.offsetHeight >= (this.getRectTop(footer) + document.body.scrollTop) - 24)
                this.absolute = true;
              if(document.body.scrollTop + window.innerHeight < (this.getRectTop(footer) + document.body.scrollTop))
                this.absolute = false;
            },
            fetchData () {
                this.$store.dispatch('partnerActions/getItems',{status:this.currentStatus});
            },
            setModerate (action) {
                this.currentAction = action;
                if(action.isValid){
                    this.$store.dispatch('popups/open', {name: this.popups.partnerModerateConfirm.name});
                } else {
                    this.currentAction = action;
                    switch (action.validError){
                        case 'field':
                            this.$store.dispatch('popups/open', {name: this.popups.partnerModerateErrorField.name});
                            break;
                        case 'percent':
                            this.$store.dispatch('popups/open', {name: this.popups.partnerModerateErrorPercent.name});
                            break;
                        case 'sum':
                            this.$store.dispatch('popups/open', {name: this.popups.partnerModerateErrorSum.name});
                            break;
                        case 'provider':
                            this.$store.dispatch('popups/open', {name: this.popups.partnerModerateErrorProvider.name});
                            break;
                    }
                }
            },
            getCityName: function(city){
                let name = '';
                if(this.cities[city]){
                    name = this.cities[city].name
                }
                return name;
            },
            getCitiesName: function(cities){
                let text = '';
                if(cities.length === 0){
                    return 'Не выбрано';
                }
                let i = 1;
                for(let index in cities) {
                    let city = [...this.cities].find(city => city.xmlId === cities[index])
                    if(city !== undefined) {

                        if(i > 3) {
                            break;
                        }
                        if (i < 3 && i < cities.length) {
                            text += city.name+', ';
                            i++;
                        } else if(i === 3 || (i === cities.length && cities.length < 3) ) {
                            text += city.name;
                            i++;
                        }
                    }
                }
                if(cities.length > 3){
                    text += ' + ещё ' + (cities.length - 3);
                }
                return text;
            },
            setFavorite: function (item) {
                this.$store.dispatch('actions/setFavorite', item);

                flyToFavorite(this.$refs.coupon);
            },
            openPartnerCouponsRemove: function (action) {
                this.currentAction = action;
                this.$store.dispatch('popups/open', {name: this.popups.partnerCouponsRemove.name});
            },
            openPartnerCouponsEdit: function (action) {
                this.currentAction = action;
                this.$store.dispatch('popups/open', {name: this.popups.partnerCouponsEdit.name});
            },
            openPartnerCouponsEditStopped: function (action) {
                this.currentAction = action;
                this.$store.dispatch('popups/open', {name: this.popups.partnerCouponsEditStopped.name});
            },
            openPartnerCouponsCancel: function (action) {
                this.currentAction = action;
                this.$store.dispatch('popups/open', {name: this.popups.partnerCouponsCancel.name});
            },
            openPartnerCouponsStop: function (action) {
                this.currentAction = action;
                this.$store.dispatch('popups/open', {name: this.popups.partnerCouponsStop.name});
            },
            openPartnerCouponsStat: function (action) {
                this.currentAction = action;
                this.$store.dispatch('popups/open', {name: this.popups.partnerCouponsStat.name});
            },
            activeAction(action) {
                this.$store.commit('partnerActions/setButtonState',{id:action.id, button: 'active', state: 'load'});
                this.$store.dispatch('partnerActions/setStatus',{id:action.id,status:this.statuses.active}).then(() => {
                    this.$store.commit('partnerActions/setButtonState',{id:action.id, button: 'active', state: 'ok'});
                    setTimeout(()=>{
                        this.$store.commit('partnerActions/setButtonState',{id:action.id, button: 'active', state: 'normal'});
                        this.$router.push('/partner/coupons/status'+this.statuses.active);
                    }, 300);
                });
            },
            archiveAction(action) {
                this.$store.commit('partnerActions/setButtonState',{id:action.id, button: 'archive', state: 'load'});
                this.$store.dispatch('partnerActions/setStatus',{id:action.id,status:this.statuses.archive}).then(() => {
                    this.$store.commit('partnerActions/setButtonState',{id:action.id, button: 'archive', state: 'ok'});
                    setTimeout(()=>{
                        this.$store.commit('partnerActions/setButtonState',{id:action.id, button: 'archive', state: 'normal'});
                        this.$router.push('/partner/coupons/status'+this.statuses.archive);
                    }, 300);
                });
            },
            copyAction(action) {
                this.$store.commit('partnerActions/setButtonState',{id:action.id, button: 'copy', state: 'load'});
                this.$store.dispatch('partnerActions/copyAction',{id:action.id}).then(() => {
                    this.$store.commit('partnerActions/setButtonState',{id:action.id, button: 'copy', state: 'ok'});
                    setTimeout(()=>{
                        this.$store.commit('partnerActions/setButtonState',{id:action.id, button: 'copy', state: 'normal'});
                        if(this.currentStatus == this.statuses.develop){
                            this.$store.dispatch('partnerActions/getItems',{status:this.currentStatus});
                        } else {
                            this.$router.push('/partner/coupons/status'+this.statuses.develop);
                        }

                    }, 300);
                });
            }
        },
    }
</script>
