<template>
    <div>
            <div class="content" v-if="action !== undefined && action.id">

            <div class="nav-top" >
                <div class="nav-top__l">
                    <template v-if="(action.lk_status_publicate=='PUBLICK' || action.lk_status_publicate=='CHECK') && action.status==statuses.active">
                        <a target="_blank" class="nav-top__category" :href="'/sid'+action.section.id" v-if="+action.section.id > 0">
                            <span class="i i-book"></span>
                            <span class="t">{{ action.section.name }}</span>
                        </a>
                    </template>
                    
                    <span v-else class="nav-top__category">
                        <span class="i" :class="actionNavIcon" style="transform: scale(0.75)"></span>
                        <span class="t">{{actionNav}} {{action.dateDiffStatus}}</span>
                    </span>
                </div>
                <div class="nav-top__r">
                </div>
            </div>



            <div class="main-title">
                <div class="main-title__wrap main-title--nowrap">
                    <div v-if="action.name" class="h1" v-html="action.name"></div>
                    <div v-else class="h1 text-color-red">Название акции не указано</div>
                </div>

                <div class="main-title__right">
                </div>
            </div>


            <section class="container">

                <lk-action-detail @changed="changeTab"  v-if="Object.keys(action).length > 0" :action="action">
                    <tab id="tab-supplier" name="Поставщик">
                         <partner-about-text-tab :partner="provider"></partner-about-text-tab>
                     </tab>

                    <!-- <statistics-detail :action="action"></statistics-detail> -->


                    <!-- <opinion-list :action-id="action.id" v-if="loadOpinion"></opinion-list> -->
                </lk-action-detail>


                <aside-sticky :margin-top="80">
                  <aside class="aside aside--right">
                    <div class="aside__sticky js-sticky" id="aside-sticky">
                        <div class="coupon-details-mini">
                            <div class="coupon-details-mini__supplier">
                                <div class="title">Поставщик</div>

                                <div class="l">
                                    <a v-if="isTabSupplier" href="javascript:void(0);" @click="setTabSupplier()">
                                        {{action.provider.name}}
                                    </a>

                                    <span v-else>
                                        {{action.provider.name}}
                                    </span>
                                </div>
                            </div>

                            <div class="coupon-details-mini__sep">
                                <div></div><div></div><div></div><div></div><div></div>
                            </div>

                            <div class="coupon-details-mini__discount">
                                <div class="title">Скидка</div>
                                <div class="t">{{ action.discountFrom }}%</div>
                            </div>

                            <div class="coupon-details-mini__price" v-if="action.realSavingsFormat">
                                <tooltip
                                     :direction="'left-center-direction'"
                                     :add-class-wrap="'dib'"
                                     :add-class-btn="'dib'"
                                     :add-style="{width: 248 + 'px',}">
                                    <span slot="tooltipBtn" class="t">
                                        ≈{{ action.realSavingsFormat }}
                                        <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                                    </span>

                                    <div slot="tooltipContent" style="font-size: 16px; ">
                                        При обращении к этому<br>
                                        поставщику средний чек<br>
                                        составляет <strong>{{ action.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>,<br>
                                        а экономия – около
                                        <strong>{{ action.realSavingsFormat }}
                                        <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>
                                    </div>
                                </tooltip>
                            </div>

                            <div class="coupon-details-mini__btn disabled-content" style="opacity: 0.2" v-if="action.realSavingsFormat">
                                <button class="btn full">
                                    <span class="t">Оформить бесплатно</span>
                                </button>
                            </div>

                            <div v-else class="coupon-details-mini__price text-color-red">
                                ?
                            </div>

                            <!--
                            <div class="coupon-details-mini__img" v-if="action.image">
                                <img :src="'//api.zemskidki.ru/'+action.image" :alt="action.name">
                            </div>
                            <div v-else class="coupon-details-mini__img --blank">
                                <span class="coupon-details-mini__img__empty">
                                    <span class="icon">
                                        <span class="i i-photo"></span>
                                    </span>
                                    <span class="t">нет фото</span>
                                </span>
                            </div> -->

                            <div class="coupon-details-mini__stat">
                                <div class="t">
                                    <span class='i i-eye'></span>
                                    <span class="t__t" v-html="action.showCounterFormat"></span>
                                </div>

                                <div class="t">
                                    <span class='i i-cart'></span>
                                    <span class="t__t" v-html="+action.statistics.sold"></span>
                                </div>

                                <!--<div class="t" v-if="action.countOpinionsFormat > 0">
                                    <span class='i i-comment'></span>
                                    <span class="t__t" >{{ action.countOpinionsFormat }}</span>
                                </div>-->
                            </div>
                        </div>
                        <!-- end coupon-details-mini -->


                        <div class="mt32 text-size-s text-center text-color-grey" v-if="action.lastEditor">
                            <div class="">Редактор:</div>
                            <div class="text-bold">{{action.lastEditor.fio}}</div>
                            <div class="text-bold" >
                                <a v-if="action.lastEditor.phone" class="text-bold" :href="'tel:'+action.lastEditor.phone.replace(/[^\+0-9]/g, '')">{{action.lastEditor.phone}}</a>
                            </div>
                        </div>
                    </div>
                </aside>
                <!-- end aside aside--right -->
                </aside-sticky>
            </section>
            <!-- end container -->


            <action-modals :action="action"></action-modals>
        </div>



        <div class="content" v-else-if="!isLoadDetail">
            <div class="main-title">
                <div class="main-title__wrap">
                    <div class="h1">Акции</div>
                </div>
            </div>


            <section class="container">

                <div class="wrapper-content">

                    <div class="sect-blank">
                        <div class="wrap text-center">
                            <div class="text-size-xl text-bolder text-color-grey mt16">К сожалению, по вашему запросу<br> ничего не найдено...</div>
                            <div class="mini-zems"></div>
                        </div>
                    </div>
                    <!-- end sect-blank -->
                </div>
            </section>
        </div>

        <popup v-if="action" :show="popups.supplierInfo.show" :name="popups.supplierInfo.name" :width="704" :add-class="'popup-supplier'" >
            <div class="popup-partner-info"><partner-about-text-tab :partner="provider"></partner-about-text-tab></div>
        </popup>
    </div>

</template>


<script>
    import Tabs from '@/components/tabs/Tabs.vue';
    import Tab from '@/components/tabs/Tab.vue';
    import Tooltip from '@/components/Tooltip.vue';
    import Popup from '@/components/modal/Popup.vue';
    import LkActionDetail from '@/components/LkActionDetail.vue';
    import ActionModals from '@/components/partner/actions/ActionModals.vue';
    import {mapState} from 'vuex';
    import PartnerAboutTextTab from '@/components/partner/PartnerAboutTextTab.vue';
    import OpinionList from '@/components/OpinionList.vue';
    import StatisticsDetail from '@/components/catalog/StatisticsDetail.vue'
    import AsideSticky from '@/components/AsideSticky.vue';

    export default {
        metaInfo() {
            return {
                title: this.title,
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "partnerPreviewAction",
        components: {
            Tooltip,
            Tabs,
            Tab,
            Popup,
            LkActionDetail,
            ActionModals,
            PartnerAboutTextTab,
            OpinionList,
            StatisticsDetail,
            AsideSticky,
        },
        data: function () {
            return {
                loadOpinion: false,
                currentTab: '',
                actionNavIcon: 'i-gear',
                isTabSupplier: false,
            }
        },
        mounted: function () {
            this.$store.dispatch('partnerActions/initDetailPage', {route: this.$route});
        },
        watch: {},
        computed: {
            ...mapState({
                action: state => state.partnerActions.detail,
                isLoadDetail: state => state.partnerActions.isLoadDetail,
                provider: state => state.partner.current,
                popups: state => state.popups.items,
                cities: state => state.cities.items,
                statuses: state => state.partnerActions.statuses,
            }),
            title () {
                return this.action.name ? this.action.name : '';
            },
            citiesAction() {
                let citiesAction = [];
                let cities = [...this.cities];
                for (let city of cities) {
                    if (city.xmlId.indexOf(this.action.city) !== -1) {
                        citiesAction.push(city)
                    }
                }
                return citiesAction;
            },
            actionNav(){
                let title = '';
                switch (+this.action.status) {
                    case this.statuses.develop:
                        title = 'В разработке';
                        break;
                    case this.statuses.active:
                        if(this.action.lk_status_publicate == 'NEW'){
                            title = 'Ждёт проверки';
                        }
                        else
                            title = 'Опубликованные акции';
                        break;
                    case this.statuses.archive:
                        title = 'Приостановлена';
                        this.actionNavIcon = 'i-pause3';
                        break;
                    case this.statuses.deleted:
                        title = 'Удалёна';
                        this.actionNavIcon = 'i-trash';
                        break;
                    case this.statuses.ban:
                        title = 'Заблокирована';
                        this.actionNavIcon = 'i-stop3';
                        break;
                    default:
                        title = '';
                        break;
                }
                return title;
            }
        },
        methods: {
            changeTab(data) {
                this.currentTab = data.tab.hash;
                if(this.currentTab === '#tab-opinions' && this.action.id){
                    this.loadOpinion = true;
                }

                this.isTabSupplier = (this.currentTab === '#tab-supplier') ? false : true;
            },
            setTabSupplier() {
                document.querySelector("[aria-controls='#tab-supplier']").click();
            },
        },
    }

</script>
