<template>
    <component :is="tag" href="javascript:void(0);" :disabled="disabled">
        <span class="w">
            <span :class="style"></span>
        </span>
        <slot></slot>
    </component>
</template>
<script>
    export default {
        props: {
            type:{
                type: String,
                default: function () {
                    return 'normal'
                }
            },
            icon:{
                type: String,
                default: function () {
                    return 'normal'
                }
            },
            tag: {
                type: String,
                default: function () {
                    return 'a'
                }
            },
        },
        computed: {
            disabled(){
                return (this.type != 'normal')?true:false;
            },
            style(){
                if(this.type == 'normal'){
                    return 'i i-'+this.icon;
                }
                if(this.type == 'load'){
                    return 'i --preloader i-'+this.icon;
                }
                if(this.type == 'ok'){
                    return 'i --preloader --ok i-'+this.icon;
                }
            }
        },
        methods:{
            send: function(){
            }
        }
    }
</script>
