<template>
    <div class="coupon">
        <router-link @click.native="activeItem(item.id)"
                     :to="{ name: 'catalogDetail', params: { id: item.id }}"
                     class="coupon__link"></router-link>

        <div class="coupon__wrap">
            <div class="coupon__img" :style="{ backgroundImage: 'url(//api.zemskidki.ru/'+item.image+')' }"></div>

            <div class="coupon__content">
                <div class="coupon__favorite">
                    <span ref="favoriteBtn" @click="setFavorite(item)" class="check-favorites" :class="{active: item.favorite}"></span>
                </div>
                
                
                
                <div class="top">
                    <div class="coupon__company">{{ provider.name }}</div>
                </div>

                <div class="coupon__name">
                    {{ item.name }}
                </div>
                
                <div class="w">
                    <div class="coupon__economy">
                        <div class="t">
                            Экономия
                            <tooltip :direction="'bottom-right-direction'"
                                     :add-class-wrap="'dib mt-2'"
                                     :add-style="{width: 376 + 'px'}">
                                <span slot="tooltipBtn" class="i i-question3 icon-btn"></span>
                                
                                <div slot="tooltipContent">
                                    <p><strong>Как рассчитывается экономия? Очень просто:</strong></p>

                                    <ul class="ul-disc-small">
                                        <li>Средний чек у данного поставщика составляет {{ item.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                        <li>Предоставляемая скидка составляет {{ item.discountFrom }}%</li>
                                        <li>{{ item.discountFrom }}% от {{ item.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span> – это примерно {{ item.realSavingsFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                    </ul>

                                    <p>То есть, при обращении к данному поставщику экономия обычно составляет ≈{{ item.realSavingsFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></p>
                                </div>
                            </tooltip>
                        </div>
                        
                        <div class="p">
                            ≈{{ item.realSavingsFormat }}
                            <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                        </div>
                    </div>
                    
                    
                    <div class="coupon__info">
                        Средний чек
                        {{ item.averageCheckFormat }}
                        <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>,
                        
                        скидка
                        <span class="n --text" v-if="item.instrumentAction">Бесплатная <br> доставка</span>
                        <span class="n" v-else>{{ item.discountFrom }}%</span>
                    </div>
                </div>
                
                
                <div class="coupon__stat">
                    <span class="elem --views">
                        <span class="i i-eye"></span>
                        <span class="t">{{ item.showCounterFormat }}</span>
                    </span>

                    <span class="elem --buy" v-if="item.statistics && item.statistics.sold">
                        <span class="i i-cart"></span>
                        <span class="t">{{ item.statistics.sold }}</span>
                    </span>
                </div>
            </div>
        </div>
        <!-- end coupon__wrap -->
    </div>
    <!-- end coupon -->
</template>

<script>
    import {flyToFavorite} from '@/helpers/animate';
    import Tooltip from '@/components/Tooltip.vue';
    
    export default {
        name: "CatalogListItem",
        components: {
            Tooltip
        },
        props: {
            item: {
                default: function () {
                    return {}
                }
            },
            provider: {
                default: function () {
                    return {}
                }
            }
        },
        methods: {
            activeItem: function (id) {
                if(id){
                    this.$store.dispatch('actions/goCoupon', {id:id});
                }
            },
            setFavorite: function (item) {
                this.$store.dispatch('actions/setFavorite', item);
                
                flyToFavorite(this.$refs.coupon);
            }
        }
    }
    
</script>